<template>
  <div>
    <section class="login">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-7 col-lg-5">
            <div class="login-wrap p-3 p-md-5">
              <a-form-model ref="formChangePhone" :model="formChangePhone" :rules="rules" @submit="changePhone"
                            @submit.native.prevent>
                <a-form-model-item prop="phone">
                  <a-input v-model.number="formChangePhone.phone" placeholder="请输入绑定的手机号" size="large">
                    <a-icon slot="prefix" type="mobile" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="code">
                  <a-input-search v-model.number="formChangePhone.code" placeholder="输入验证码" size="large"
                                  @search="sendCode">
                    <a-icon slot="prefix" type="code" style="color:rgba(0,0,0,.25)"/>
                    <a-button slot="enterButton" :disabled="disable">
                      {{ btnText }}
                    </a-button>
                  </a-input-search>
                </a-form-model-item>
                <a-form-model-item prop="newPhone">
                  <a-input v-model.number="formChangePhone.newPhone" placeholder="请输入新手机号" size="large">
                    <a-icon slot="prefix" type="mobile" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item prop="confirmNewPhone">
                  <a-input v-model.number="formChangePhone.confirmNewPhone" placeholder="请确认新手机号"
                           size="large">
                    <a-icon slot="prefix" type="mobile" style="color:rgba(0,0,0,.25)"/>
                  </a-input>
                </a-form-model-item>
                <a-form-model-item>
                  <a-button
                      type="primary" block
                      html-type="submit"
                      size="large"
                  >
                    提 交
                  </a-button>
                </a-form-model-item>
              </a-form-model>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: 'changePhone',
    data() {
      let validatorPhone = (rule, value, callback) => {
        // 正则判断手机号格式的格式，正则判断失败抛出错误，否则直接callback()
        if (!/^1[2-9]\d{9}$/.test(value)) {
          callback(new Error("手机号格式不正确!"));
        } else {
          callback();
        }
      }

      let checkCode = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入验证码！'));
        }

        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字！'));
        } else if (!/^\d{6}$/g.test(value)) {
          callback(new Error('验证码位数不正确！'));
        } else {
          callback();
        }
      }

      let validateNewPhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入新手机号！'));
        } else if (!/^1[2-9]\d{9}$/.test(value)) {
          callback(new Error('新手机号格式不正确！'));
        } else if (this.formChangePhone.confirmNewPhone !== '') {
          this.$refs.formChangePhone.validateField('confirmNewPhone');
          callback();
        } else {
          callback();
        }

      }

      let validateNewPhone2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入新的手机号！'));
        } else if (value !== this.formChangePhone.newPhone) {
          callback(new Error('两次输入的新手机号不一致!'));
        } else {
          callback();
        }
      };

      return {
        formChangePhone: {
          phone: '',
          code: '',
          newPhone: '',
          confirmNewPhone: '',
        },
        disable: false, //验证码60秒倒计时是否显示
        btnText: '获取验证码',
        isLoading: false, //提交修改手机号提交按钮的加载动画
        rules: {
          phone: [
            {required: true, message: '请输入手机号！', trigger: 'blur'},
            {validator: validatorPhone, trigger: 'change'}
          ],
          code: [{validator: checkCode, trigger: 'change'}],
          newPhone: [{validator: validateNewPhone, trigger: 'change'}],
          confirmNewPhone: [{validator: validateNewPhone2, trigger: 'change'}]
        }
      };
    },
    methods: {
      changePhone() {
        this.$refs.formChangePhone.validate(valid => {
          if (!valid) {
            this.$message.error('请先规范填写表单！');
            return;
          }

          this.isLoading = true;
          const {phone, code, newPhone} = this.formChangePhone;
          
          if (phone === newPhone) {
            this.$message.error('新手机号不能和旧手机号相同！');
            return false;
          }

          request({
            method: 'put',
            url: '/ouser/changePhone',
            params: {oldPhone: phone, code: code, newPassword: newPhone}
          }).then(res => {
            if (res.code === 1) {
              storage.clearAll();
              this.$router.replace({name: 'changePhoneSuccess'});
            } else {
              this.$message.error(res.msg);
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
      sendCode() {
        this.$refs.formChangePhone.validateField(['phone'], error => {
          if (error) {
            this.$message.error('获取验证码失败！');
            return;
          }

          this.disable = true;
          let count = 60;
          let timer = setInterval(() => {
            this.btnText = count + ' 秒后重新发送';
            count--;
            if (count < 0) {
              this.disable = false;
              this.btnText = '获取验证码';
              clearInterval(timer);
            }
          }, 1000)

          request({
            method: 'get',
            url: '/ouser/before/getCode',
            params: {phone: this.formChangePhone.phone}
          }).then(res => {
            if (res.code === 1) {
              this.$message.success('验证码发送成功，请注意查收！', 5);
            } else {
              this.$message.error(res.msg);
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        })
      },
    },
    activated() {
      this.isLoading = false;
    }
  }
</script>

<style scoped>
  .login {
    min-height: 720px;
    background-color: #f8f9fd;
  }

  .login-wrap {
    margin-top: 15%;
    position: relative;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 34px -15px rgb(0 0 0 / 24%);
  }

  .login-wrap h3 {
    font-weight: 350;
  }


  .col-lg-5 {
    flex: 0 0 auto;
    width: 35%;
  }


  .col h4 {
    text-align: center;
  }

  .active h4 {
    color: green;
    border-bottom: 2px solid green;
  }

  .ant-form {
    margin-top: 5%;
  }

  /deep/ .ant-input-lg {
    height: 50px;
  }

  .ant-btn-lg {
    height: 48px;
  }


  .form-group {
    margin-bottom: 1rem;
  }

  .ant-input-search-button {
    height: 50px;
  }

</style>